.App {
  background: rgb(2, 0, 36);
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(2, 0, 36, 1)), color-stop(32%, rgba(9, 9, 121, 1)), to(rgba(0, 212, 255, 1)));
  background: -webkit-linear-gradient(bottom, rgba(2, 0, 36, 1) 0%, rgba(9, 9, 121, 1) 32%, rgba(0, 212, 255, 1) 100%);
  background: -o-linear-gradient(bottom, rgba(2, 0, 36, 1) 0%, rgba(9, 9, 121, 1) 32%, rgba(0, 212, 255, 1) 100%);
  background: linear-gradient(0deg, rgba(2, 0, 36, 1) 0%, rgba(9, 9, 121, 1) 32%, rgba(0, 212, 255, 1) 100%);
  text-align: center;
  margin: 0 auto;
  padding-top: 100px;
  min-height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.Header {
  height: 56px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  background-color: #cbf1ec9c;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 90;
}

.Link {
  text-decoration: none;
  font-weight: bold;
  color: #000094
}

.Link:active {
  text-decoration: none;
  color: #000094
}


