.SelectContainer {
  max-height: 500px;
  overflow-y: auto;
  max-width: 245px;
  margin: 0 auto;
}

.UserItem {
  display: -ms-flexbox;
  display: flex;
  background: #fff;
  margin: 0 auto;
  padding: 5px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
}
.UserItem:hover {
  background: #ddd;
}

.UserItem img {
  max-width: 45px;
}
.UserItem span {
  margin-left: 10px;
}

.Input {
  margin: 0 auto;
  border-radius: 8px;
  padding: 10px;
  width: 225px;
  font-size: 18px;
  border: 1px solid #ccc;
}

.Input.Opened {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}

.Input:focus {
  outline: none;
}


